.footer_container {
  height: fit-content;
  width: 100%;
  background-color: #072125;
  color: white;
  margin-top: -4%;
}

#tsparticles {
  object-fit: initial;
  height: 100%;
  width: 95%;
  padding: none;
  position: absolute;
  z-index: -1;
  background-color: white;
}
.particle {
  height: 100%;
  width: 100%;
  position: fixed;
  background-color: red;
}

.content_footer {
  width: 70%;
  padding: 35px;
  margin-left: 130px;
  display: flex;
}

.content_footer h2 {
  font-size: 30px;
  margin-top: 15px;
  color: aqua;
}

.footer .footer_container .content_footer .footer_header_top {
  margin-left: 0%;
}

.footer .footer_container .content_footer .footer_header_top h2 {
  margin-top: 5%;
}

.footer .footer_container .content_footer .content {
  margin: 0;
  margin-top: 5px;
  margin-right: -20%;
}

.content_footer .footer_header_top h3 {
  font-size: 18px;
  color: white;
}

.content_footer h3 i {
  font-size: 18px;
  margin-right: 5px;
  margin-top: 2px;
}

.content {
  margin-right: -30px;
}

.content h2 {
  font-size: 20px;
  margin-right: 100px;
  margin-top: 0px;
}

.content button {
  margin-right: 10px;
  margin-top: -10px;
}

.footer_bottom {
  margin-top: -10px;
}

.btn_shadow_footer {
  padding: 12px 15px;
  border-radius: 50%;
  transition: 0.3s all ease;
  cursor: pointer;
  color: black;
  transition: 0.5s;
}

.btn_shadow_footer {
  background-color: linear-gradient(145deg, #e2e8ec, #ffffff);
}

.btn_shadow_footer:hover {
  background: aqua;
  color: black;
  transform: translateY(-10px);
}

.footer_bottom {
  font-size: 18px;
}

@media (max-width: 768px) {
  .footer .footer_container .content_footer {
    display: flex;
    flex-direction: row;
    margin: 0;
    margin-top: 0px;
    height: fit-content;
    width: 100%;
  }

  .footer .footer_container .content_footer .footer_header_top {
    margin-left: 5%;
  }

  .footer .footer_container .content_footer .footer_header_top h2 {
    margin-top: 5%;
  }

  .content_footer .footer_header_top h3 {
    font-size: 15px;
    color: white;
  }

  .footer_header_top {
    display: flex;
    flex-direction: column;
  }

  .footer .footer_container .content_footer h2 {
    margin: 0;
  }

  .footer .footer_container .content_footer .content {
    margin: 0;
    margin-top: 10px;
    margin-right: 8%;
  }

  .footer .footer_container .content_footer .content .btn_footer {
    margin-top: -10px;
  }

  .content_footer h2 {
    font-size: 24px;
    margin-top: 15px;
    color: aqua;
  }

  .content h2 {
    font-size: 18px;
  }

  .content_footer h3 {
    margin-left: 0px;
    font-size: 12px;
    text-align: left;
  }

  .footer_bottom hr {
    margin-bottom: 5px;
  }

  .footer_bottom {
    font-size: 15px;
  }
}

@media (max-width: 400px) {
  .footer .footer_container .content_footer {
    display: flex;
    flex-direction: column;
    margin: 0;
    margin-top: 0px;
    height: fit-content;
    width: 100%;
  }

  .content_footer .footer_header_top h3 {
    font-size: 15px;
    color: white;
  }

  .footer_header_top {
    display: flex;
    flex-direction: column;
  }

  .footer .footer_container .content_footer h2 {
    margin: 0;
  }

  .footer .footer_container .content_footer .footer_header_top {
    margin-left: 0%;
  }

  .footer .footer_container .content_footer .content {
    margin: 0;
    margin-top: 10px;
  }

  .footer .footer_container .content_footer .content .btn_shadow_footer {
    margin-top: -5px;
  }

  .content_footer h2 {
    font-size: 24px;
    margin-top: 15px;
    color: aqua;
  }

  .content h2 {
    font-size: 20px;
  }

  .content_footer h3 {
    margin-left: 0px;
    font-size: 12px;
    text-align: left;
  }

  .footer_bottom hr {
    margin-bottom: 5px;
  }

  .footer_bottom {
    font-size: 14px;
  }
}
