.hero #background_video {
  object-fit: initial;
  height: 100%;
  width: 95%;
  padding: none;
  position: absolute;
  z-index: -1;
  /* -o-filter: blur(0px);
    filter: blur(0px);
    -webkit-filter: blur(0px); */
}

#tsparticles {
  object-fit: initial;
  height: 100%;
  width: 95%;
  padding: none;
  position: absolute;
  z-index: -1;
  background-color: white;
}

.particle {
  height: 100%;
  width: 100%;
  position: fixed;
  background-color: red;
}

.hero {
  position: relative;
  margin-top: 4%;
  margin-left: 0;
}

.hero .hero_container {
  /* background-color: white; */
}

.hero h3 {
  font-weight: 400;
  font-size: calc(20 * 0.0625rem);
  letter-spacing: calc(3 * 0.0625rem);
  margin-top: 5rem;
}

.hero h1 {
  font-size: calc(50 * 0.0625rem);
  font-weight: bold;
}

.hero h1 span {
  color: #ff014f;
}

.hero h2 {
  font-size: calc(40 * 0.0625rem);
  font-weight: bold;
  color: #ff014f;
}

.hero h2 span {
  color: black;
}

.hero_btn {
  /* margin-top: 20%;
 */
}

.hero_btn h4 {
  font-weight: 400;
  letter-spacing: calc(2 * 0.0625rem);
  color: black;
  font-size: calc(25 * 0.0625rem);
  margin: calc(30 * 0.0625rem) 0;
}

.hero_btn button {
  margin-right: calc(15 * 0.0625rem);
}

.hero .left {
  margin-left: 10rem;
  margin-top: 2rem;
}

.hero .right {
  width: 7rem;
  position: relative;
}

