.right .right_img img {
  margin-left: 3rem;
  width: calc(289 * 0.0625rem);
  /* calc(290 *0.0625rem); */
  /* 70%; */
  height: 0%;
}

p {
  letter-spacing: calc(2 * 0.0625rem);
  font-size: calc(20 * 0.0625rem);
  margin-top: 5%;
  color: black;
  font-weight: 400;
}

.cursor {
  animation: blink 1s linear infinite forwards;
}

@media (max-width: 1300px) {
  .hero .hero_container .right_img {
    margin: 0;
    margin-left: 5rem;
    margin-right: 1rem;
    margin-top: 1rem;
  }
}

@media (max-width: 1150px) {
  .hero .hero_container .right_img {
    margin: 0;
    margin-left: 2rem;
    margin-right: 1rem;
    margin-top: 1rem;
  }

  .hero .left {
    margin-left: 3rem;
  }

  .hero .right_img {
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
    box-shadow: 1rem 1rem 2rem #cbcbcb, -1rem -1rem 2rem #ffffff;
    border-radius: calc(10 * 0.0625rem);
    height: fit-content;
    width: calc(330 * 0.0625rem);
    margin-top: 2rem;
    margin-left: 2rem;
    margin-bottom: 0rem;
  }
}

@media (max-width: 800px) {
  .hero {
    margin: 2%;
    margin-top: 8%;
    margin-bottom: 8%;
  }

  .hero h1 {
    font-size: calc(40 * 0.0625rem);
  }

  .hero h2 {
    font-size: calc(30 * 0.0625rem);
    font-weight: bold;
    color: #ff014f;
  }

  .hero h3 {
    font-size: calc(20 * 0.0625rem);
  }

  .hero_container {
    display: flex;
    flex-direction: column;
  }

  .hero .hero_container .left {
    margin-left: 8%;
    margin-bottom: 5%;
  }

  .hero_btn .col_1 h4 {
    margin-left: calc(-10 * 0.0625rem);
  }

  .hero_btn .col_1 {
    margin-left: calc(15 * 0.0625rem);
  }

  .hero_btn button {
    margin-right: calc(15 * 0.0625rem);
    margin-bottom: 5%;
  }

  .hero .hero_container .right_img {
    margin: 0;
    margin-left: 12%;
    margin-right: 0%;
    margin-top: 0%;
    margin-top: 0%;
  }

  .hero .right {
    width: 70%;
    margin-left: 4%;
    margin-bottom: calc(5 * 0.0625rem);
  }

  .hero .right_img {
    width: 100%;
  }

  .right .right_img {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .right.top img {
    margin-left: 0%;
    margin-top: 0%;
  }

  .right .right_img img {
    margin-bottom: calc(15 * 0.0625rem);
    width: 60%;
    height: 60%;
  }
}

